import React, { Component } from 'react';
import Page from './Page';
import AceEditor from 'react-ace';
import * as d3 from 'd3';
import 'brace/mode/plain_text';
import 'brace/theme/chrome';

export default class ChartPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      height: window.innerHeight,
    }
  }

  onResize = () => {
    this.setState({
      width: window.innerWidth,
      height: window.innerHeight,
    });
  }

  componentDidMount() {
    window.addEventListener('resize', this.onResize, false);
    const width = this.container.clientWidth;
    const height = this.container.clientHeight-5;
    this.props.initialize(this.svg, { width, height, });
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.onResize);
  }

  componentDidUpdate() {
    const width = this.container.clientWidth;
    const height = this.container.clientHeight-5;
    this.props.update(this.svg, { width, height, });
  }

  componentWillReceiveProps(nextProps) {
  }

  render() {
    return (
      <div>
        <Page
          title={this.props.title}
        >
          <div
            ref={n => this.container = n}
            style={{
              display: 'inline-block',
              marign: 0,
              padding: 0,
              width: '48%',
              marginRight: 10,
            }}
          >
            <svg
              ref={n => this.svg = d3.select(n)}
              width="100%"
              height={this.state.height-150}
              style={{
                border: 'solid 1px lightgray',
              }}
            >
              <g
                ref={n => this.base = d3.select(n)}
                className="base"
              />
              <g
                ref={n => this.axis = d3.select(n)}
                className="axis"
              />
            </svg>
          </div>
          <AceEditor
            style={{
              display: 'inline-block',
              border: 'solid 1px lightgray',
            }}
            mode="text"
            theme="chrome"
            width="50%"
            value={this.props.text}
            onChange={this.props.onChangeText}
            height={this.state.height-150}
            setOptions={{
              //useSoftTabs: true,
              newLineMode: 'unix',
              //showInvisibles: true,
              //enableBasicAutocompletion: false,
            }}
          />
        </Page>
      </div>
    )
  }
}

ChartPage.defaultProps = {
  title: '',
  text: '',
  initialize: () => {},
  update: () => {},
  onChangeText: () => {},
}
