import React, { Component } from 'react';
import ChartPage from '../../../components/ChartPage';
import * as d3 from 'd3';

//Force Simulation の世界
//https://qiita.com/junkoda/items/2d12ecdd3b4b5c99d994

export default class Page extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: this.props.text,
      dataSet: {
        data: [],
        xmin: 0,
        ymin: 0,
        xmax: 100,
        ymax: 100,
      },
    }
  }

  dataSet = () => {
    const r = [];
    const l = this.state.text.split('\n')
    const nodes = [];
    const links = [];
    let max = 0;
    {
      const m = {}
      const findIndex = (nodes, key) => {
        let index = -1;
        nodes.some( (v, i) => {
          if (v.label == key) {
            index = i;
            return true;
          }
          return false;
        })
        return index;
      }
      for (var i in l) {
        const t = l[i].replace(/(\s+)/g, ' ').split(' ');
        if (t.length >= 3) {
          if (typeof m[t[0]] === 'undefined') {
            m[t[0]] = {}
            nodes.push({ id: nodes.length, label: t[0] })
          }
          if (typeof m[t[1]] === 'undefined') {
            m[t[1]] = {}
            nodes.push({ id: nodes.length, label: t[1] })
          }
          links.push({
            source: findIndex(nodes, t[0]),
            target: findIndex(nodes, t[1]),
            value: Number(t[2]),
          })
          if (max < Number(t[2])) max = Number(t[2]);
        } else
        if (t.length > 0 && t[0] != '') {
          nodes.push({ id: nodes.length, label: t[0] })
        }
      }
    }
    return {
      max,
      nodes,
      links: links.map( v => {
        v.value /= max;
        return v;
      }),
    }; 
  }

  initialize = (svg, { width, height }) => {
    const { xmin, xmax, ymin, ymax } = this.state.dataSet;

    this.xScale = d3.scaleLinear()
      .domain([0, width])
      .range([0, width]);
    this.yScale = d3.scaleLinear()
      .domain([0, height])
      .range([0, height]);

    this.xAxis = d3.axisBottom(this.xScale)
    .tickSize(height)
    .tickFormat(d => d)

    this.yAxis = d3.axisRight(this.yScale)
    .tickSize(width)
    .tickFormat(d => d)

    this.setState({
      dataSet: this.dataSet(),
    })
  }

  update = (svg, { width, height }) => {
    const self = this;
    const { xmin, xmax, ymin, ymax, links, nodes, } = this.state.dataSet;
    const length = Math.max(width, height)

    this.xScale
      .domain([0, width])
      .range([0, width]);
    this.yScale
      .domain([0, height])
      .range([0, height]);

    var force = d3.forceSimulation()
      .force('link', d3.forceLink().id(function(d) { return d.id; }))
      .force('charge', d3.forceManyBody())
      .force('center', d3.forceCenter(width / 2, height / 2));
    
    force
      .nodes(nodes)
      .on('tick', ticked);

    force.force('link')
        .links(links)
        .distance(function() { return length/5;})
        // .strength(function(){ return 2; })

    force.force('collide', d3.forceCollide().radius(function(d) { return 5; }).iterations(2))

    const g = svg.select('g')

    function ticked() {
      g.selectAll('line.link')
      .data(links)
      .exit()
      .remove()

      g.selectAll('line.link')
      .data(links)
      .enter()
      .append('line')
      .classed('link', true)
      .attr('fill', 'none')
      .attr('stroke', '#999')
      .attr('stroke-opacity', 0.6)

      g.selectAll('line.link')
        .attr('x1', d => self.xScale(d.source.x))
        .attr('y1', d => self.yScale(d.source.y))
        .attr('x2', d => self.xScale(d.target.x))
        .attr('y2', d => self.yScale(d.target.y))
        .attr('stroke-width', d => 1)

      g.selectAll('circle')
      .data(nodes)
      .exit()
      .remove()

      g.selectAll('circle')
      .data(nodes)
      .enter()
      .append('circle')
      .attr('cx', d => self.xScale(d.x))
      .attr('cy', d => self.yScale(d.y))
      .attr('r', d => 5)
      .attr('fill', '#ACD8FF')
      .attr('stroke', '#fff')
      .attr('stroke-width', 1.5)

      g.selectAll('circle')
      .attr('cx', d => self.xScale(d.x))
      .attr('cy', d => self.yScale(d.y))
      .attr('r', d => 5)

      g.selectAll('text.label')
      .data(nodes)
      .exit()
      .remove()

      g.selectAll('text.label')
      .data(nodes)
      .enter()
      .append('text')
      .classed('label', true)
      .attr('x', d => self.xScale(d.x))
      .attr('y', d => self.yScale(d.y)+5)
      .text(d => d.label)
      .attr('font-size', '10')
      .attr('text-anchor', 'middle')
      .attr('alignment-baseline', 'hanging')
      .attr('fill', 'black')
      .style('pointer-events', 'none')
      .style('user-select', 'none')

      g.selectAll('text.label')
      .attr('x', d => self.xScale(d.x))
      .attr('y', d => self.yScale(d.y)+5)
      .text(d => d.label)

      function dragstarted() {
        if (!d3.event.active) force.alphaTarget(0.3).restart();
        d3.event.subject.fx = d3.event.subject.x;
        d3.event.subject.fy = d3.event.subject.y;
      }

      function dragged() {
        d3.event.subject.fx = d3.event.x;
        d3.event.subject.fy = d3.event.y;
      }

      function dragended() {
        if (!d3.event.active) force.alphaTarget(0);
        d3.event.subject.fx = null;
        d3.event.subject.fy = null;
      }

      function dragsubject() {
        return force.find(d3.event.x, d3.event.y);
      }

      g.call(d3.drag()
        .subject(dragsubject)
        .on('start', dragstarted)
        .on('drag', dragged)
        .on('end', dragended));
    }
  }

  onChangeText = (text) => {
    this.setState({ text, }, () => {
      this.setState({ dataSet: this.dataSet() }, () => {
      })
    });
  }

  render() {
    return (
      <ChartPage
        title={this.props.title}
        initialize={this.initialize}
        update={this.update}
        onChangeText={this.onChangeText}
        text={this.state.text}
      />
    )
  }
}

Page.defaultProps = {
  title: '共起ネットワーク',
  text: `A\tB\t6
B\tC\t5
C\tD\t4
D\tB\t3
`,
}
